.userList {
  width: 100%;
  text-align: center;
  margin-bottom: 140px;
  &__title {
    @extend %h1;
    margin-bottom: 50px;
    text-align: center;
  }

  &__list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 29px;
    margin-bottom: 50px;

    @include onTablet {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 16px;
    }

    @include onMobile {
      grid-template-columns: repeat(1, minmax(0, 1fr));
      gap: 20px;
    }
  }

  &__btn {
    margin: 0 auto;
  }
}
